import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ManagerolesComponent } from './manageroles/manageroles.component';
import { ManageusersComponent } from './manageusers/manageusers.component';
import { AddrolesComponent } from './addroles/addroles.component';
import { AddusersComponent } from './addusers/addusers.component';
import { ViewrolesComponent } from './viewroles/viewroles.component';
import { ViewusersComponent } from './viewusers/viewusers.component';
import { AuthGuard } from '@app/auth/auth.guard';

const routes: Routes = [
  {
    path:'',
    children: [
      {
        path: 'manageroles',
        component: ManagerolesComponent,
        data: {
          title: 'Manage Roles',
          urls: [
            { title: 'Manage Roles', url: '/manageroles' }
          ]
        },canActivate: [AuthGuard]
      },
      {
        path: 'addroles',
        component: AddrolesComponent,
        data: {
          title: 'Add Roles',
          formid:1,
          urls: [
            { title: 'Add Roles', url: '/addroles' }
          ]
        },canActivate: [AuthGuard]
      },
      {
        path: 'viewroles',
        component: ViewrolesComponent,
        data: {
          title: 'View Roles',
          urls: [
            { title: 'View Roles', url: '/viewroles' }
          ]
        },
      },
      {
        path: 'manageusers',
        component: ManageusersComponent,
        data:{
          title: 'Manage Users',
          formid:2,
          urls: [
            { title: 'Manage Users', url: '/manageusers' }
          ]
        },canActivate: [AuthGuard]
      },
      {
        path: 'addusers',
        component: AddusersComponent,
        data:{
          title: 'Add Users',
          urls: [
            { title: 'Add Users', url: '/addusers' }
          ]
        }
      },
      {
        path: 'viewusers',
        component: ViewusersComponent,
        data:{
          title: 'View Users',
          urls: [
            { title: 'View Users', url: '/viewusers' }
          ]
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class NewenterpriseadminRoutingModule { }
